import Login  from '@/modal/login'
export default {
  name: 'login',
  // layout: 'default',
  components:{
    Login
  },
  data: () => ({
      // 벨류
    nickname        : '',                // 닉네임
    email           : '',                // 이메일
    pass            : '',                // 비밀번호
    pwVerify        : '',                // 비밀번호 확인

    // 버튼
    nickClear       :false,              // 닉네임 지우기 버튼
    emailClear      :false,              // 이메일 지우기 버튼
    pwClear         :false,              // 비밀번호 지우기 버튼

    // 조건 검사
    disabled        :false,              // 폼 버튼 활성화
    sameNick        :true,               // 닉네임 중복 (임시 데이터)
    ableNick        :null,               // 닉네임 중복 검사
    ableEmail       :null,               // 이메일 입력 조건
    ablePw          :null,               // 비밀번호 입력 조건
    ablePwVerify    :null,               // 비밀번호 일치 검사

    // 이벤트
    emailActive     :false,              // 이메일 입력 감지        
    pwActive        :false,              // 비밀번호 입력 감지
    passwordType    :'password',         // 비밀번호 타입
    write           :true,               // 인풋아이콘 on
    password        :true,               // 비밀번호 아이콘 on
    passClear       :false,              // 비밀번호 아이콘 on
    secret          :false,              // 비밀번호 가리기 on off
  }),
  methods: {
          // 이메일 ============================================
    // 포커스 온
    emailFocus(){
      // 입력시 제목 on
      this.emailActive = true;

      // 형식에 안 맞을 경우 전부 리셋
      if(this.ableEmail == false){
        this.email      = '';
        this.ableEmail  = null;
        this.emailClear = false;
        this.emailActive = false;}
    },
    AbleEmail(){
      let emVal         = this.email; // 벨류
      let returnEmail   =             // 이메일 데이터 일치하는지
      /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.{1}[a-zA-Z]{2,4}$/i;
      //^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
      
      // 형식 안 맞을 경우 에러
      if(!returnEmail.test(emVal) && emVal != '')
        this.ableEmail  = false
      // 형식 맞으면 통과
      else if(returnEmail.test(emVal))
        this.ableEmail  = true
      // 아무 글도 없을 경우 리셋
      else if(emVal == '')
        this.ableEmail  = null

      // 글자 입력시 x버튼 on, off 
      if(emVal != ''){
        this.emailClear = true
        this.emailActive = true;}
      else{
        this.emailClear = false 
        this.emailActive = false;}
    },
    // 비밀번호 ============================================
    // 포커스 온
    passFocus(){
      // 형식에 안 맞을 경우 전부 리셋
      if(this.ablePw == false){
        this.pass    = '';
        this.ablePw  = null;
        this.pwClear = false;}
    },
    AblePass(){
      let pwVal       = this.pass;  // 벨류
      let returnPass  =             // 형식
      /^(?=.*[A-Za-z])(?=.*\d)(?=.*[$@$!%*#?&])[A-Za-z\d$@$!%*#?&]{8,16}$/;

      // 형식 안 맞을 경우 에러
      if(!returnPass.test(pwVal) && pwVal != '')
        this.ablePw = false
      // 형식 맞으면 통과
      else if(returnPass.test(pwVal))
        this.ablePw = true

      // 글자 입력시 x버튼 on, off
      if(this.pass != ''){
        //this.ablePw   = true;
        this.pwClear  = true;
        this.pwActive = true;}
      else{
        this.pwClear  = false;
        this.pwActive = false;}
    },
    // 버튼 ============================================
    // 이메일 리셋
    clearEmail(){
      this.email        = '';     // 벨류
      this.ableEmail    = null;   // 입력 조건
      this.emailClear   = false;  // 지우기 버튼
    },

    // 비밀번호 리셋
    clearPassword(){
      this.pass         = '';     // 비밀번호 벨류
      this.pwVerify     = '';     // 비밀번호 확인 벨류
      this.ablePw       = null    // 비밀번호 조건
      this.pwClear      = false;  // 비밀번호 지우기 버튼
      this.ablePwVerify = null;   // 비밀번호 일치 검사
    },

    // 비밀번호 시크릿
    secretToggle(){
      if(this.secret == true){
        this.secret = false;
        this.passwordType = 'password'
      }else{
        this.secret = true;
        this.passwordType = 'text'} 
    },
    // 폼 버튼 활성화
    ableBtn(){
      let Email = this.ableEmail == true;
      let Pw    = this.ablePw    == true;

      if(Email && Pw)
        this.disabled = true;
      else
        this.disabled = false;
    },
    // 폼 버튼 활성화 후 비밀번호 형식 조건 체크
    checkForm(){
      let vm = this
      if(vm.disabled == true){
        let pwVal      = vm.pass;  // 벨류
        let returnPass =           // 형식
        /^(?=.*[A-Za-z])(?=.*\d)(?=.*[$@$!%*#?&])[A-Za-z\d$@$!%*#?&]{8,16}$/;

        // 형식 안 맞을 경우 에러
        if(!returnPass.test(pwVal) && pwVal != '')
          {vm.ablePw = false}
      }
    }
  }
}
